<template>
  <div id="card-list">
    <!-- 三列 1：1：1 -->
    <transition name="card-fade">
    <div class="card-row" v-if="rowShow.row1">
      <div class="card-column">
        <card class="el-card" :card-vo="postList[0]"></card>
      </div>
      <div class="card-column">
        <card class="el-card" :card-vo="postList[1]"></card>
      </div>
      <div class="card-column">
        <card class="el-card" :card-vo="postList[9]"></card>
      </div>
    </div>
    </transition>
    <!-- 两列 1：1-->
    <transition name="card-fade">
    <div class="card-row" v-if="rowShow.row2">
      <div class="card-column">
        <!-- <card class="el-card-row2" :card-vo="postList[3]"></card> -->
        <full-card class="el-card-row2" :card-vo="postList[4]"></full-card>
      </div>
      <div class="card-column">
        <card class="el-card-row2" :card-vo="postList[3]"></card>
      </div>
    </div>
    </transition>
    <!-- 三列 1:1:1-->
    <transition name="card-fade">
    <div class="card-row" v-if="rowShow.row3">
      <div class="card-column">
        <card class="el-card" :card-vo="postList[5]"></card>
      </div>
      <div class="card-column">
        <card class="el-card" :card-vo="postList[6]"></card>
      </div>
      <div class="card-column">
        <card class="el-card" :card-vo="postList[7]"></card>
      </div>
    </div>
    </transition>
    <!-- 两列 1：2 -->
    <transition name="card-fade">
    <div class="card-row" v-if="rowShow.row4">
      <div class="card-column">
        <card class="el-card" :card-vo="postList[8]"></card>
      </div>
      <div class="card-column">
        <full-card class="el-card-row3" :card-vo="postList[2]"></full-card>
      </div>
    </div>
    </transition>
  </div>
</template>

<script>
const Card = () => import('@/components/common/Card')
const FullCard = () => import('@/components/common/FullCard')
export default {
  components: {
    'card': Card,
    'full-card': FullCard
  },
  props: {
    rowShow: Object
  },
  data () {
    return {
      isCard: true,
      postList: [
        {image: '/static/image/sea.jpg', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']},
        {image: '/static/image/beach.jpg', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']},
        {image: '/static/image/developer.jpg', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']},
        {image: '/static/image/computer.jpg', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']},
        {image: '/static/loading/5-160914192R6-50-.gif', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']},
        {image: '/static/loading/5-160914192R5.gif', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']},
        {image: '/static/loading/5-160914192R6.gif', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']},
        {image: '/static/loading/5-160914192R6-52.gif', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']},
        {image: '/static/loading/5-160914192R4-51.gif', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']},
        {image: '/static/image/jscode.jpg', title: '', desc: '', author: '', date: '', need: '', tag: ['1', '2']}
      ]
    }
  },
  computed: {
    // row1 () {
    //   return this.rowShow.row1
    // },
    // row2 () {
    //   return this.rowShow.row2
    // },
    // row3 () {
    //   return this.rowShow.row3
    // },
    // row4 () {
    //   return this.rowShow.row4
    // }
  },
  watch: {
    // row1 (val1, val2) {
    //   console.log('watch:' + val1 + '|' + val2)
    // },
    // row2 (val1, val2) {
    //   console.log('watch:' + val1 + '|' + val2)
    // },
    // row3 (val1, val2) {
    //   console.log('watch:' + val1 + '|' + val2)
    // },
    // row4 (val1, val2) {
    //   console.log('watch:' + val1 + '|' + val2)
    // }
  }
}
</script>

<style scoped>
#card-list{
  width: 100%;
}
.img-contain{
  height: 210px;
  overflow: hidden;
  text-align: center;
  border-radius: 7px 7px 0px 0px;
  background: linear-gradient(to right bottom, #6d327c, #485DA6, #00a1ba, #01b18e, #32b37b);
}
img{
  width: 100%;
  height: 210px;
  object-fit: cover;
  transition: all 0.7s;
}
.card-row{
  /* border: 1px dashed green; */
  padding: 15px 25px 15px 25px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  align-content: stretch;
}
.el-card-style{
  padding: 0;
  height: 100%;
  background: rgba(255, 255, 255, 1);
  /* background-color: aliceblue; */
  box-shadow: 0px 2px 2px rgba(18,18,19,.5);
  border-radius: 7px;
  transition: all .3s ease 0s;
}
@media screen and (max-width: 659px){
  .card-column{
    border: 1px dashed black;
    padding: 10px 0px 10px 0px;
  }
}
@media screen and (min-width:660px) and (max-width: 1023px){
  .card-column{
    border: 1px dashed yellow;
    padding: 0px 3px 0px 3px;
  }
  .el-card{
    max-width: 190px;
  }
  .el-card-row2{
    max-width: 585px;
  }
}
@media screen and (min-width:1024px) and (max-width:1299px){
  .card-column{
    border: 1px dashed red;
    padding: 0px 15px 0px 15px;
  }
  .el-card{
    max-width: 250px;
  }
  .el-card-row2{
    max-width: 585px;
  }
}
@media screen and (min-width: 1300px){
  .card-column{
    border: 1px dashed green;
    padding: 10px 20px 10px 20px;
  }
  .el-card{
    max-width: 375px;
  }
  .el-card-row2{
    max-width: 585px;
    min-width: 585px;
  }
  .el-card-row3{
    width: 790px;
  }
}
img:hover{
  transform: scale(1.1,1.1);
  opacity: 0.8;
}
.card-column:hover{
  cursor: pointer;
}
.el-card-style:hover{
  transform: translateY(-5px);
  box-shadow: 4px 4px 5px rgba(18,18,19,.3),-4px 0px 5px rgba(18,18,19,.3);
}
.card-fade-enter, .card-fade-leave-to{
  transform: translateY(50px);
  opacity: 0.2;
}
.card-fade-enter-active, .card-fade-leave-active{
  transition: all 1s ease 0s;
}
</style>



